<template>
    <div class="cardList">
        <article class="card" v-for="(item, idx) in items" :key="{idx}">
            <div class="card__item">
                <span class="card__img"  :style="{ backgroundImage: `url('${item.imgsrc}')` }"></span>
                <div class="card__text">
                    <div class="card__badgeBox">
                        <i v-bind:class="['badge', item.type]">{{item.type}}</i>
                    </div>
                    <h1 class="card__subject"><span>{{item.subject}}</span></h1>
                    <div class="card__subinfo">
                        <span class="card__part">참여도 <b>{{item.part}}%</b></span>
                        <span class="card__date">{{item.date}}</span>
                    </div>
                </div>
            </div>
            <a :href="item.url" v-if="item.link" target="_blank">
                <span class="card__btn" v-if="item.link"> <span v-if="item.local!==false">Local source</span> <span v-if="item.local==false">Link</span></span>
            </a>
            <span class="card__textNolink" v-else-if="item.link == false">no link</span>
        </article>
    </div>
</template>

<script>
export default {
    props: {
        items: { type: Array, default: () => [] }
    }
};
</script>

